/* BATCH CARD DESIGN */
.batch_card {
    align-items: center;
    box-shadow: 0 5px 20px 0 rgba(23, 44, 82, 0.1);
    border: 1px solid;
    border-color: #f1f8ff;
    background-color: #fff;
    overflow: hidden;
    padding: 15px;
    margin-bottom: 10px;
}
.batch_card .batch_pro_img {
    float: left;
}
.batch_card .batch_pro_img img {
    width: 200px;
    height: auto;
}

/* right content start */
.batch_card .batch_pro_cnt {
    overflow: hidden;
    padding: 0 0 0 30px;
}

.batch_card .batch_number {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.batch_card .batch_number h2 {
    font-size: 18px;
    font-family: Roboto;
    font-weight: 500;
    margin-bottom: 5px;
}
.batch_card .batch_number span {
    color: #f0151f;
    font-family: Roboto;
    font-weight: 500;
}
.batch_card .batch_name {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}
.batch_card .batch_name h2 {
    color: #f0151f;
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    margin-bottom: 5px;
}
.batch_card .batch_inst {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}
.batch_card .batch_inst h5 {
    font-size: 15px;
    font-weight: 500;
    font-family: "Roboto";
    color: rgb(92, 114, 125);
    margin-bottom: 5px;
}
.batch_card .batch_inst h5 span {
    font-size: 14px;
    font-weight: 400;
    font-family: "Roboto";
    color: rgb(92, 114, 125);
}
.batch_card .batch_rate {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}
.batch_card .batch_rate h5 {
    font-size: 15px;
    font-weight: 500;
    font-family: "Roboto";
    color: #f0151f;
    margin-bottom: 5px;
}
.batch_card .batch_rate h5 span {
    font-size: 14px;
    font-weight: 400;
    font-family: "Roboto";
    color: rgb(92, 114, 125);
}
.view_more_btn {
    margin-bottom: 8px;
}
.view_more_btn a {
    font-size: 16px;
    font-weight: 500;
    font-family: "Roboto";
    margin-bottom: 5px;
}

.batch_card_mob {
    display: none;
}
