.carousel-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.carousel-wrapper {
    display: flex;
    width: 100%;
    position: relative;
}

.carousel-content-wrapper {
    overflow: hidden;
    width: 100%;
    /* height: 100%; */
}

.carousel-content {
    display: flex;
    transition: all 250ms linear;
    -ms-overflow-style: none; /* hide scrollbar in IE and Edge */
    scrollbar-width: none; /* hide scrollbar in Firefox */
}

/* hide scrollbar in webkit browser */
.carousel-content::-webkit-scrollbar,
.carousel-content::-webkit-scrollbar {
    display: none;
}

.carousel-content > * {
    width: 100%;
    flex-shrink: 0;
    flex-grow: 1;
}

.carousel-content.show-2 > * {
    width: 50%;
}

.carousel-content.show-3 > * {
    width: calc(100% / 3);
}

.carousel-content.show-4 > * {
    width: calc(100% / 4);
}

.left-arrow,
.right-arrow {
    position: absolute;
    z-index: 999;
    /* transform: translateY(-50%); */
    width: 25px;
    height: 25px;
    display: block;
    top: 45%;
    border-radius: 50%;
    background-color: #f0151f;
    border: 1px solid #ddd;
    color: #fff;
}
.left-arrow:focus,
.right-arrow:focus {
    color: #f0151f;
    background-color: #fff;
    border: 1px solid #f0151f;
}
.left-arrow {
    left: -15px;
}

.right-arrow {
    right: -15px;
}

