.custom-file-upload {
  position: absolute;
  bottom: 15px;
  right: 25%;
  left: 64%;
}
.custom-file-input {
  color: transparent;
  width: 40px;
}
.custom-file-input::-webkit-file-upload-button {
  visibility: hidden;
}
.custom-file-input::before {
  content: "\f093";
  color: #fff;
  display: inline-block;
  background: #f0151f;
  border: 1px solid #999;
  border-radius: 3px;
  padding: 5px 8px;
  outline: none;
  border: none;
  white-space: nowrap;
  cursor: pointer;
  text-shadow: 0px;
  font-weight: 700;
  font-size: 10pt;
}
.custom-file-input:hover::before {
  border-color: black;
}
.custom-file-input:active {
  outline: 0;
}
.custom-file-input:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

.img-wrap img {
  width: 160px;
  height: 160px;
  overflow: hidden;
  background: #e5e5e5;
  border-radius: 50%;
}
.author-img p {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  color: #676767;
  font-size: 14px;
}

